.wrapper {
  padding: 20px;
  height: 93vh;

  .head {
    height: 8%;
    padding: 10px;
    display: flex;
    justify-content: space-around;
    gap: 20px;

    .mode {
      display: flex;
      gap: 10px;
    }

    .urgency {
      display: flex;
      gap: 10px;

    }
  }

  .body {
    height: 92%;

    .table {
      height: 94%;
      width: 100%;
      overflow: auto;
      padding: 10px;
      border: 2px black solid;
      border-radius: 25px;
    }

    .pag {
      height: 6%;
      padding: 5px;
    }
  }
}